  background: #fff;
  border-radius: 12px;
  @apply border-solid border border-brightGreen;
  padding: 6px;
  padding-top: 10px;
  text-align: center;
  color: transparent;

  position: absolute;
  @apply bottom-[60vw] 2xs:bottom-[48vw] xl:bottom-[44vw] 2xl:bottom-[42vw];
  /* bottom: 240px; */
  /* width: 260px; */
  @apply w-[230px] 2xs:w-[260px] lg:w-[400px] 2xl:w-[350px];
  @apply text-xs lg:text-lg;
  right: 0;
  left: 0;
  margin-right: auto;
  margin-left: auto;
  @apply max-h-[100px] lg:max-h-[200px] xl:max-h-[100px];

  box-sizing: border-box;
}

.bubble {
  background: #fff;
  border-radius: 12px;
  @apply border-solid border border-brightGreen;
  padding: 6px;
  padding-top: 10px;
  text-align: center;
  color: #000;

  position: absolute;
  bottom: 50vw;
  @apply bottom-[60vw] 2xs:bottom-[48vw] xl:bottom-[44vw] 2xl:bottom-[42vw];
  @apply w-[230px] 2xs:w-[260px] lg:w-[400px] 2xl:w-[350px];
  @apply text-xs lg:text-lg;
  right: 0;
  left: 0;
  margin-right: auto;
  margin-left: auto;
  max-height: 100px;
  @apply max-h-[100px] lg:max-h-[200px] xl:max-h-[100px];
  overflow-y: scroll;
  z-index: 15;

  box-sizing: border-box;
}

.bubbleBottomRight:before {
  content: "";
  width: 0px;
  height: 0px;
  position: absolute;
  @apply border-solid border-l-[24px] border-r-[12px] border-t-[12px] border-b-[20px] border-brightGreen border-r-transparent border-b-transparent;

  /* border-left: 24px solid #000;
    border-right: 12px solid transparent;
    border-top: 12px solid #000;
    border-bottom: 20px solid transparent; */
  right: 70px;
  bottom: -24px;
}

.bubbleBottomLeft:before {
  content: "";
  width: 0px;
  height: 0px;
  position: absolute;
  @apply border-solid border-l-[24px] border-r-[12px] border-t-[12px] border-b-[20px] border-brightGreen border-r-transparent border-b-transparent;

  /* border-left: 24px solid #000;
    border-right: 12px solid transparent;
    border-top: 12px solid #000;
    border-bottom: 20px solid transparent; */
  left: 35px;
  bottom: -24px;
  transform: scaleX(-1);
}
